



































import { Component, Vue, Prop } from "vue-property-decorator";
import Intro from "./components/Intro.vue";
import Goals from "./components/Goals.vue";
import Experience from "./components/Experience.vue";
import Education from "./components/Education.vue";
import Skills from "./components/Skills.vue";
import Interests from "./components/Interests.vue";
import Footer from "./components/Footer.vue";

class Section {
  constructor(public name: string, public icon: string) {}
}

@Component({
  components: {
    Intro,
    Goals,
    Experience,
    Education,
    Skills,
    Interests,
    Footer
  }
})
export default class App extends Vue {
  drawer = false;
  sections = [
    new Section("intro", "dashboard"),
    new Section("goals", "dashboard"),
    new Section("experience", "dashboard"),
    new Section("education", "dashboard"),
    new Section("skills", "dashboard"),
    new Section("interests", "dashboard")
  ];
  goToSection(sectionName: string) {
    this.$vuetify.goTo("#" + sectionName, { offset: -72 });
    this.drawer = false;
  }
}
