
























































import { Component, Vue } from "vue-property-decorator";

class SocialData {
  constructor(public name: string, public icon: string, public link: string) {}
}

@Component
export default class Footer extends Vue {
  icons = [
    new SocialData("GitHub", "fa-github", "https://github.com/pawel-schmidt"),
    new SocialData(
      "StackOverflow",
      "fa-stack-overflow",
      "https://stackoverflow.com/users/5101816/pxsx"
    ),
    new SocialData(
      "Linked-in",
      "fa-linkedin",
      "https://www.linkedin.com/in/pawe%C5%82-schmidt/"
    ),
    new SocialData(
      "CodePen",
      "fa-codepen",
      "https://codepen.io/pxsx/pens/public/"
    )
  ];
  emailClicksCount = 5;
  get emailHrefHidden() {
    return this.emailClicksCount > 0;
  }
  get emailHref() {
    if (this.emailHrefHidden) {
      return "";
    }
    return "mailto:paw3l.schmidt@gmail.com";
  }
  get tooltipText() {
    if (this.emailHrefHidden) {
      return `Click ${this.emailClicksCount} more times`;
    }
    return "Click to open email client app";
  }
}
